.container {
    width: 1160px !important;
}
body {
    font-family: "微软雅黑"
}
.header {
    height: 30px;
    line-height: 30px;
    background-color: #333;
    color: #fff;
    font-size: 12px;
    .pull-right {
        a {
            position: relative;
            margin-left: 50px;
            display: inline-block;
            i {
                display: block;
                position: absolute;
                left: -40px;
                top:50%;
                margin-top: -15px;
                width: 30px;
                height: 30px;
                background: url('../images/bo.jpg') no-repeat center;
            }
            i.twI {
                background: url('../images/wei.jpg') no-repeat center;
            }
        }
    }
}
a:focus, a:hover {
    color: red;
    text-decoration: underline;
}
a {
    color: #fff;
    transition: all .2s ease-in-out;
}
.nav>li>a {
    padding: 10px 0;
    width: 100px;
    text-align: center
}
.nav>li>a:focus, .nav>li>a:hover {
    text-decoration: none;
    background-color: transparent;
    border-bottom: 5px solid #99d128 !important;
}
.navbars {
    a {
        color: #000;
    }
    .attr a{
        border-bottom: 5px solid #99d128!important;
    }
    a:focus, a:hover {
        color: #000;
        text-decoration: underline;
    }
    .navbar-nav {
        float:right;
    }
    .navbar-nav>li>a {
        padding-top: 35px;
        padding-bottom: 30px;
        border-bottom: 5px solid #FFF;
    }
    h1 {
        margin:0;
    }
    .logo {
        display: block;
        height: 90px;
        width: 249px;
        background: url('../images/logo.jpg') no-repeat center;
        background-size: 100%;
        overflow: hidden;
    }
}

.banner {
    div {
        width: 100%;
        height: 399px;
    }
}
